@import url(https://fonts.googleapis.com/css?family=Inconsolata:400,700&display=swap);
body {
  margin: 0;
  font-family: 'Inconsolata';
  color: #2C3E50;
  background: -webkit-gradient(linear, left top, left bottom, from(#FD746C), to(#2C3E50));
  background: linear-gradient(to bottom, #FD746C, #2C3E50);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding: 25px 25px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  background: white;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

a {
  text-decoration: none;
}

a {
  color: #2C3E50;
}

a:hover {
  color: #FD746C;
}


.Project img {
  width: 400px;
}

h2 {
  padding: 30px;
}

.project-container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.presentation {
  margin: 50px;
  text-align: center;
}

h1 {
  font-size: 35px;
}

h2 {
  font-size: 30px;
}

p {
  font-size: 20px;
}

.project {
  font-size: 25px;
  text-align: center;
}

.description {
  width: 600px;
  margin: 0 auto;
  text-align: center;
}
.main-title {
    text-align: center;
}

.about-container {
    margin: 10px 150px;
    padding-bottom: 10px;
}
.Nav__container {
  display: flex;
  justify-content: space-between;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-bottom-color: #FD746C;
  padding: 10px;
}

.Links {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin: 0;
  min-width: 0;
  flex-flow: wrap;
}

.Nav img {
  width: 150px;
  height: 70px;
}

a {
  margin: 10px;
}

.Nav ul {
  list-style-type: none;
  display: flex;
}

